// mint-ui 框架
import Vue from 'vue';
import { 
  Swipe, SwipeItem, Toast
} from 'mint-ui';
import 'mint-ui/lib/swipe/style.css';
import 'mint-ui/lib/toast/style.css';
Vue.component('mt-swipe', Swipe);
Vue.component('mt-swipe-item', SwipeItem);

// vue-awesome
import icon from 'vue-awesome/components/Icon';
import 'vue-awesome/icons/angle-right';
import 'vue-awesome/icons/exclamation';
import 'vue-awesome/icons/circle-o';

// 自己封装 ICON
import CARWASH from './../../assets/CAR-WASH.vue'; // 洗车
import GAS2 from './../../assets/GAS2.vue';          // 加油
import park from './../../assets/park.vue';        // 停车
import charge from './../../assets/charge.vue';    // 充电
import Vehiclemaintenance from './../../assets/Vehicle-maintenance.vue'; // 保养
import nurse from './../../assets/nurse.vue';      // 养护
import rescue from './../../assets/rescue.vue';    // 紧急救援
import violation from './../../assets/violation.vue'; // 违章查询

import tabbar from './../../components/TabBar.vue';

import convertDate from './../../utils/convertDate';
import ajaxs from './ajaxs.js';

export default {
  name: 'home',

  components: {
    // SVG组件
    CARWASH, GAS2, park, charge, Vehiclemaintenance, 
    nurse, rescue, violation, Toast,
    // 自写组件
    tabbar,
    // vue-awesome
    icon
  },

  data () {
    return {
      pictures: { // 所有图片
        logo: require('./../../assets/YCPD.png'),
        fourPlate: [ // 新增四个板块入口图片 每隔一个小时强制刷新一次
          `http://p6ygud9kn.bkt.clouddn.com/YCPD/wx20/fourPlate1.png?${convertDate.dateToYYYYmmDDhhMM00(new Date())}`,
          `http://p6ygud9kn.bkt.clouddn.com/YCPD/wx20/fourPlate2.png?${convertDate.dateToYYYYmmDDhhMM00(new Date())}`,
          `http://p6ygud9kn.bkt.clouddn.com/YCPD/wx20/fourPlate3.png?${convertDate.dateToYYYYmmDDhhMM00(new Date())}`,
          `http://p6ygud9kn.bkt.clouddn.com/YCPD/wx20/fourPlate4.png?${convertDate.dateToYYYYmmDDhhMM00(new Date())}`,
        ]
      },

      bannerHeight: ( // 设置轮播图的高度 按照 375: 160 比例
        document.body.offsetWidth || 
        document.documentElement.clientWidth || 
        window.innerWidth
      ) / 375 * 160,

      swiperlist: [{ // 轮播图的组件数据 每隔一个小时强制刷新一次
        alt: "养车频道-banner",
        src: `http://p6ygud9kn.bkt.clouddn.com/YCPD/wx20/banner01.jpg?${convertDate.dateToYYYYmmDDhhMM00(new Date())}`, // 放置在七牛云储存里, 方便替换
      }],

      selected: '外卖',
    }
  },
  
  computed: {
    isLogin() { // 用户信息 如果未登录 则设为 false
      return this.$store.state.user.info
    }
  },

  methods: {
    tabbarClick(selectIndex) { // 底部 tabbar 点击触发的事件
      if (selectIndex === 'my') { // 如果 标识为 my 则跳转到个人中心
        this.$router.push('/user');
      }
    },

    jumpToUrl(url) { // 页面的跳转 根据 url 进行跳转
      window.location.href = url;
    },

    showNotCompleted() { // 弹出没有完成的提示
      Toast({
        message: '即将上线',
        duration: 1000
      });
    }
  },
}




